@import url(https://use.typekit.net/ebh1hal.css);
/* Root styles go here
  Styles that apply to body or anything.
 */

* {
  font-family: futura-pt, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}


.FormCenter {
    width: 300px;
    margin: auto;
    background-color: #FFFFFF;
    padding: 100px 200px;
  }
  
  .FormTitle {
    color: #f27649;
    font-weight: lighter;
    margin-bottom: 40px;
    font-size: 11pt;
    text-align: center;
    text-transform: uppercase;
  }
  
  .FormTitle__Link {
    color: #707C8B;
    text-decoration: none;
    display: inline-block;
    font-size: 1.7em;
    margin: 0 10px;
    padding-bottom: 5px;
  }
  
  .FormField {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .FormField__Label {
    display: block;
    text-transform: uppercase;
    font-size: .9em;
    color: #4e74a6;
    text-align: center;
  }
  .FormField__Input {
    width: 100%;
    background-color: transparent;
    border: none;
    color: #4e74a6;
    outline: none;
    border-bottom: 1px solid #f2d16d;
    font-size: 1em;
    font-weight: 300;
    padding-bottom: 5px;
    margin-top: 20px;
    text-align: center;
  }
  
  .FormField__Input::-webkit-input-placeholder {
    color: #616E7F;
  }
  
  .FormField__Input::-ms-input-placeholder {
    color: #616E7F;
  }
  
  .FormField__Input::placeholder {
    color: #616E7F;
  }
  
  .FormField__Button {
    background-color: #f27649;
    color: white;
    border: none;
    outline: none;
    border-radius: 25px;
    padding: 15px 25px;
    font-size: .8em;
    font-weight: 500;
  
  }
  
  .FormField__Link {
    color: #4e74a6;
    text-decoration: none;
    border-bottom: 0.5px solid #225E62;
    padding-bottom: 5px;
    padding-top:5px;
    font-size: small;
  }
  
  .FormField__CheckboxLabel {
    color: #4e74a6;
    font-size: small;
  }
  
  .FormField__Checkbox {
    position: relative;
    top: 1.5px;
  }
  
  .FormField__TermsLink {
    color: #4e74a6;
    border-bottom: 1px solid #199087;
    text-decoration: none;
    display: inline-block;
    padding-bottom: 2px;
    margin-left: 5px;
  }
